.pieceSelectorContainer {
    background-color: grey;
    pointer-events: all;
    height: 100%;
    width: 100%;
    padding: 5px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}

.pieceGroup {
    background-color: tan;
    pointer-events: all;
    flex: 1;
    width: 100%;
    display: grid;
    align-items: center;
    justify-items: center;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 5px;
    padding: 5px;
    box-sizing: border-box;
    border-radius: 10px;
}

.p1 {
    composes: pieceGroup;
    background-color: #000080
}

.p2 {
    composes: pieceGroup;
    background-color: #008000;
}

.piece {
    object-fit: contain;
    min-width: 0px;
    min-height: 0px;
    max-width: 100%;
    max-height: 100%;
    border-radius: 10px;
}

.available {
    composes: piece;
    cursor: pointer;
    background-color: teal;
}

.unavailable {
    composes: piece;
    background-color: grey;
}